import React , { createContext , useState } from 'react'

import b1 from  "../assets/b1.jpg"
import b3 from  "../assets/b3.jpg"
import b7 from  "../assets/b7.jpg"
import bo1 from  "../assets/bo1.jpg"
import bo2 from  "../assets/bo2.jpg"
import bo3 from  "../assets/bo3.jpg"
import jeans from  "../assets/jeans.jpg"
import jeans2 from  "../assets/jeans2.jpg"
import jeans3 from  "../assets/jeans3.jpg"
import l1 from  "../assets/l1.jpg"
import l2 from  "../assets/l2.jpg"
import l4 from  "../assets/l4.jpg"

export const ProductsContext = createContext();

const ProductsContextProvider = (props) => {

const [products] = useState([ 
    { id:1 , name:"b1", price: 300, image: b1, status: 'hot' } ,
    { id:2 , name:"b3", price: 400, image: b3, status: 'hot' } ,
    { id:3 , name:"b7", price: 300, image: b7, status: 'new' } ,
    { id:4 , name:"bo1", price: 600, image: bo1, status: 'hot' } ,
    { id:5 , name:"bo2", price: 300, image: bo2, status: 'new' } ,
    { id:6 , name:"bo3", price: 200, image: bo3, status: 'hot' } ,
    { id:7 , name:"jeans", price: 300, image: jeans, status: 'hot' } ,
    { id:8 , name:"jeans2", price: 900, image: jeans2, status: 'new' } ,
    { id:9 , name:"jeans3", price: 300, image: jeans3, status: 'hot' } ,
    { id:10 , name:"l1", price: 1300, image: l1, status: 'new' }, 
    { id:11, name:"l2", price: 350, image: l2, status: 'hot' } ,
    { id:11, name:"l4", price: 350, image: l4, status: 'hot' } ,
]);


    return (
        <ProductsContext.Provider value={{ products: [...products] }}>
            {props.children}
        </ProductsContext.Provider>
    )
}
export default ProductsContextProvider;



